body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.search-form-container {
  text-align: center;
  z-index: 1000;
  background-color: green;
  padding: 10px 0;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.search-form {
  max-width: 600px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: inline-flex;
}

.search-form input {
  width: 300px;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  cursor: pointer;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  flex-wrap: wrap;
  gap: 15px;
  margin-top: 20px;
  padding: 20px;
  display: flex;
}

.photo-card {
  width: calc(25% - 15px);
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  justify-content: space-between;
  padding: 10px;
  display: flex;
}

.info-item {
  font-size: 14px;
}

.load-more {
  cursor: pointer;
